import { TripSortView } from '../view/trip-sort';
import { render, replace } from '../framework/render';
export default class TripSortPresenter {
    #container;
    #target;
    #getCurrentPoints;
    #currentSort = 'sort-day';
    #sortHandler;
    constructor(props) {
        this.#container = props.container;
        this.#target = this.#getTarget();
        this.#getCurrentPoints = props.getCurrentPoints;
        this.#sortHandler = props.sortHandler;
        this.render();
    }
    #sortPoints = (value) => {
        const sortByPrice = (priceOne, priceTwo) => priceOne.basePrice - priceTwo.basePrice;
        const sortByTime = (timeOne, timeTwo) => (timeOne.dateTo.getTime() - timeOne.dateFrom.getTime()) - (timeTwo.dateTo.getTime() - timeTwo.dateFrom.getTime());
        const sortByDate = (dayOne, dayTwo) => dayOne.dateFrom.getTime() - dayTwo.dateFrom.getTime();
        const sorts = new Map([
            ['sort-price', sortByPrice],
            ['sort-day', sortByDate],
            ['sort-time', sortByTime]
        ]);
        return this.#getCurrentPoints().sort((a, b) => sorts.get(value)(a, b));
    };
    getSortedPoints = (sort) => {
        if (sort) {
            this.#currentSort = sort;
            const newTarget = this.#getTarget();
            replace(newTarget, this.#target);
            this.#target = newTarget;
            return this.#sortPoints(sort);
        }
        return this.#sortPoints(this.#currentSort);
    };
    #updateSort = (sort) => {
        this.#currentSort = sort;
        this.#sortHandler();
    };
    #getTarget = () => new TripSortView(this.#updateSort);
    render = () => {
        render(this.#target, this.#container);
    };
}
